/* Сброс */
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
  
  .main-container {
    font-family: 'Inter', sans-serif;
    padding: 2rem;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .header {
    text-align: center;
    margin-bottom: 2rem;
  }
  
  .header h1 {
    font-size: 2rem;
    font-weight: 600;
    color: #f1f5f9;
  }
  
  .header p {
    color: #94a3b8;
    margin-top: 0.5rem;
    font-size: 0.95rem;
  }
  
  /* Вкладки */
  .tabs {
    margin-top: 1rem;
    display: flex;
    gap: 1rem;
    justify-content: center;
  }
  
  .tabs button {
    background-color: #334155;
    color: #f1f5f9;
    border: none;
    padding: 0.6rem 1.2rem;
    border-radius: 0.6rem;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .tabs button.active,
  .tabs button:hover {
    background-color: #3b82f6;
  }
  
  /* Каталог */
  .catalog {
    width: 100%;
    max-width: 1024px;
    margin-top: 2rem;
  }
  
  .catalog h2 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
    color: #f1f5f9;
    text-align: center;
  }
  
  .product-grid {
    display: grid;
    gap: 1.5rem;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  }
  
  .product-card {
    background-color: #1e293b;
    border-radius: 1rem;
    padding: 1.25rem;
    box-shadow: 0 12px 20px rgba(0, 0, 0, 0.3);
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    transition: transform 0.3s ease;
  }
  
  .product-card:hover {
    transform: translateY(-5px);
  }
  
  .product-card h3 {
    color: #f1f5f9;
    font-size: 1.2rem;
  }
  
  .product-card p {
    color: #cbd5e1;
    font-size: 0.95rem;
  }
  
  /* Футер */
  .footer {
    margin-top: 3rem;
    text-align: center;
    font-size: 0.9rem;
    color: #94a3b8;
  }
  
  /* Светлая тема */
  body.light .main-container {
    background-color: #f9fafb;
    color: #0f172a;
  }
  
  body.light .header h1,
  body.light .catalog h2 {
    color: #0f172a;
  }
  
  body.light .header p,
  body.light .footer {
    color: #475569;
  }
  
  body.light .tabs button {
    background-color: #e2e8f0;
    color: #0f172a;
  }
  
  body.light .tabs button.active,
  body.light .tabs button:hover {
    background-color: #3b82f6;
    color: white;
  }
  
  body.light .product-card {
    background-color: #ffffff;
    box-shadow: 0 12px 20px rgba(0, 0, 0, 0.1);
  }
  
  body.light .product-card h3 {
    color: #0f172a;
  }
  
  body.light .product-card p {
    color: #475569;
  }
  .product-card {
    background-color: #1e293b;
    border-radius: 1rem;
    padding: 1.25rem;
    box-shadow: 0 12px 20px rgba(0, 0, 0, 0.3);
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    align-items: center;
    transition: transform 0.3s ease;
  }
  
  .product-image {
    width: 100%;
    max-width: 200px;
    height: auto;
    border-radius: 0.75rem;
    margin-bottom: 0.5rem;
    object-fit: cover;
  }
  .product-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
    gap: 1.5rem;
    padding: 1rem;
  }
  
  .product-card {
    background-color: #1e293b;
    border-radius: 1rem;
    padding: 1rem;
    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3);
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    transition: transform 0.2s ease;
  }
  
  .product-card:hover {
    transform: translateY(-5px);
  }
  
  .product-image {
    width: 100%;
    height: 180px;
    object-fit: cover;
    border-radius: 0.6rem;
  }
  